@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{
  margin:0;
  padding: 0;
  border: 0;
  box-sizing:border-box;
}

body{
  font-family: 'Roboto', sans-serif;
}

.container{
  max-width: 800px;
  margin:0 auto ;
 
}

h1,h3{
  color: #06000F;
}

#informacion{
  display: flex;
}

p{
  text-align: justify;
}

@media screen and (max-width: 500px) {
  body{
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-wrap: wrap;
  }

  .container{
    max-width: 500px;
  }

  #informacion{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
  }
}

header{
    background-image: url(/hojadevida/static/media/header.261b3dd6.png);
    background-repeat: no-repeat;
    display: flex;
    padding: 40px ;
}

.foto{
    flex-basis: 40%;
    text-align: center;
}

.foto img{
    max-width: 180px;
    border-radius: 50%;
    border: 7px solid hsl(272, 26%, 41%);
}

.texto{
    flex-basis: 60%;
}

.texto h3{
    font-weight: 500;
}

.texto h1{
    font-weight: 420;

}

.nombre{
    color:#6A2A91;
}
.contacto{
    border-right: 3px solid hsl(273, 34%, 73%);
    padding: 10px;
    flex-basis: 50%;
}

.info {
    margin-bottom: 5px;
}

.info i{
    background-color: hsl(273, 34%, 73%);
    padding: 5px;
    border-radius: 50%;
    margin-right: 5px;
    font-size: 20px;
}

.ref {
    margin-bottom: 5px;
}

.ref i{
    background-color: hsl(273, 34%, 73%);
    padding: 5px;
    border-radius: 50%;
    margin-right: 5px;
    font-size: 20px;
}

h2{
    text-align: center;
}

hr{
    border-bottom: 4px solid hsl(273, 34%, 73%);
    width: 50%;
    margin: 5px auto;
}


.acerca {
    padding: 18px;
    flex-basis: 50%;
}

.TituloCar{
    margin: 5px 5px 5px 0px;
}


progress[value]::-webkit-progress-bar {
    background-color: #c2d2e9;
}


progress[value]::-webkit-progress-value {
    background-color: hsl(273, 34%, 73%);
    
}

.info{
    font-weight: 600;
}

.Titulo2{
    margin: 5px 5px 5px 0px;
}

.Titulo{
    padding: 5px 5px 5px 0px;
    
}

.titlab{
    background-color:hsl(273, 34%, 73%);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 5px 20px;
}

.estudio p{
    padding-left:40px ;
    margin: 10px 0;
}


footer{
    background-image: url(/hojadevida/static/media/footer.599a2df7.png);
    min-height: 189px;
}

.imagen {
    float: right;
}
